.container {
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(300px,auto));

  @for $i from 5 through 0 {
    &__box:nth-child(#{6-$i}) {
      background-color: rgb($i * 20% , 0%, 0%);
    }
  }
}

.container__box {
  min-width: 300px;
  color: #fff;
  line-height: 300px;
}



@media screen and (min-width:1200px) {
  .container {
    grid-template-columns: repeat(3,minmax(300px,1fr));
  }
}
